import uniIcon from "@/components/uni-icons/uni-icons.vue";
export default {
	components:{
		uniIcon
	},
	props:{
		title:{
			type: String,
			default: '请输入'
		},
		propItems:{
			type: Array
			
		}
	},
	onReady:function(){
		this.items = this.propItems
	},
	data() {
		return {
			// items: [{
			// 		id: 'USA',
			// 		label: '价格太高'
			// 	},
			// 	{
			// 		id: 'CHN',
			// 		label: '布面不干净'
					
			// 	},
			// 	{
			// 		id: 'BRA',
			// 		label: '买家不满意'
			// 	}],
			items:[],
			current: 0,
			bannerShow:false,
			
		};
	},
	methods:{
		radioChange:function(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].id === evt.target.value) {
					this.current = i;
					break;
				}
			}
			//this.$emit('testInput',evt)
			
		},
		closeBanner:function(){
			this.bannerShow=false;
		},
		show:function(){
			this.bannerShow=true;
		},
		confirmBanner:function(){
			// if (this.content !==''){
			// 	this.$emit('input',this.content)
			// }else{
			// 	this.$emit('input',this.items[0].label)
			// }
			this.$emit('input',this.current)
			this.bannerShow = false
		}
	}
	
}
